import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
//api
import { updatePost, getDetail } from '../../lib/api/post';
//component
import FormBody from '../TextField/Form';

const Edit = () => {
  // apistate
  const [value, setValue] = useState('')
  // react-router-domid
  const query = useParams();
  const navigate = useNavigate();
  // query
  useEffect(() => {
    handleGetData(query)
  },[query])
  // idapi/api/v1/posts/:id
  const handleGetData = async (query) => {
    try {
      const res = await getDetail(query.id)
      console.log(res.data)
      // state
      setValue({
        name: res.data.post.name,
        content: res.data.post.content
      })
    } catch (e) {
      console.log(e)
    }
  }
  // state
  const handleChange = (e) => {
    setValue({
      ...value,
      [e.target.name]: e.target.value
    })
  }
  // idparameterapi
  const handleSubmit = async (e) => {
    e.preventDefault()
    try {
      const res = await updatePost(query.id, value)
      console.log(res)
      // '/timeline'
      navigate('/timeline')
    } catch(e) {
      console.log(e)
    }
  }

  return(
    <FormBody
      handleChange={handleChange}
      handleSubmit={handleSubmit}
      value={value}
      buttonType='edit'
    />
  )
}
export default Edit